import { getDefaultOptions, request } from 'api/helpers';

const resource = 'api/houses';

export const apiHouseGet = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'GET',
  };
  return request(url, options);
};

export const apiHousePost = async (serviceUrl, house) => {
  const url = `${serviceUrl}/${resource}`;
  const options = {
    ...getDefaultOptions(),
    method: 'POST',
    body: house ? JSON.stringify(house) : null,
  };
  return request(url, options);
};

export const apiHousePut = async (serviceUrl, id, house) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'PUT',
    body: house ? JSON.stringify(house) : null,
  };
  return request(url, options);
};

export const apiHouseDelete = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'DELETE',
  };
  return request(url, options);
};
